// fonts
@import url('https://use.typekit.net/bgr1vie.css');
// colours
body {
  background-color: #fefefe;
}
p,h1,h2,h3,.offcanvas-title {
  font-family: freight-big-pro, serif;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 1.2px;
}

.top {
  position: fixed;
  right: 0;
  width: 100%;
  height: 100%;
  background-image: url('../uploads/flowershires.jpeg');
  background-position: top right;
  background-repeat: no-repeat;
  background-size: 800px;
  z-index: -1;
}
.bottom {
  position: fixed;
  width: 100%;
  height: 200px;
  background-image: url('../uploads/flowersbottom.jpg');
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: 600px;
}
.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.section-rule {
  margin-left: auto;
  margin-right: auto;
  border-bottom: 1px solid black;
  width: 20%;
  align-self: flex-end;
  margin-bottom: 40px;
}
$primary: #f08e80;
$background: #DEEBE4;

@media (max-width: 575.98px) {
  .top {
    background-size: 100%;
  }
}
